import React, {useCallback, useState} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {addMenuItem, deleteMenuItemAC, updateMenuItem} from '../redux/actions';
import {AppState} from '../redux/reducers';
import {Button, Input, Paper, TextField, Typography} from "@mui/material";

interface MenuItem {
    id: number;
    name: string;
    price: number;

}

export function MenuEditor() {

    const menuItems = useSelector<AppState, MenuItem[]>(
        (state) => state.menuItems
    );

    const [addingItem, setAddingItem] = useState(false);

    const handleAddClick = () => {
        setAddingItem(true);
    }

    return (
        <div>
            {addingItem ? (
                <AddForm

                    onCancel={() => setAddingItem(false)}
                />
            ) : (
                <Button onClick={handleAddClick}
                        variant="contained"
                        size="large"
                        sx={{m: 3,}}>
                        
                </Button>
            )}

            {menuItems.map(item => (
                <MenuItem
                    key={item.id}
                    item={item}
                />
            ))}


        </div>
    );

}

function MenuItem({item}: { item: MenuItem }) {

    const [editing, setEditing] = useState(false);

    const dispatch = useDispatch();

    const handleDeleteMenuItem = useCallback(() => {
        dispatch(deleteMenuItemAC(item.id));

    }, []);

    if (editing) {
        return <EditForm
            item={item}
            onCancel={() => setEditing(false)}
        />
    }

    return (
        <Paper component="div" sx={{p: 2, m: 2,}}>
            <Typography>
                {item.id} - {item.name} - {item.price}
            </Typography>
            <Button onClick={() => setEditing(true)}
                    variant="outlined"
                    size="small"
                    sx={{m: 1,}}
            >
            </Button>
            <Button onClick={handleDeleteMenuItem}
                    variant="outlined"
                    color={"warning"}
                    size="small"
                    sx={{m: 1,}}
            >
            </Button>
        </Paper>

    );

}

function EditForm({item, onCancel}: {
    item: MenuItem,
    onCancel: () => void
}) {

    const [name, setName] = useState(item.name);
    const [price, setPrice] = useState(item.price);

    const dispatch = useDispatch();

    const handleUpdate = useCallback(() => {
        dispatch(updateMenuItem({...item, name, price}));
        onCancel();
    }, [name, price]);


    return (
        <div style={{display: 'flex', alignItems: 'center'}}>
            <TextField
                size="small"
                variant="standard"
                label=""
                sx={{
                    p: 2,
                    m: 2,
                    width: '30%'
                }}
                type="text"
                value={name}
                onChange={e => setName(e.target.value)}
            />

            <TextField
                size="small"
                variant="standard"
                label=""
                sx={{
                    p: 2,
                    m: 2,
                    width: '30%'
                }}
                type="number"
                value={price}
                onChange={e => setPrice(Number(e.target.value))}
            />

            <Button onClick={handleUpdate}
                    variant="outlined"
                    size="small"
                    sx={{
                        p: 2,
                        m: 2,
                        width: '15%'
                    }}>
                
            </Button>

            <Button onClick={onCancel}
                    variant="outlined"
                    size="small"
                    sx={{
                        p: 2,
                        m: 2,
                        width: '15%'
                    }}>
                
            </Button>
        </div>
    );

}

function AddForm({onCancel}: { onCancel: () => void }) {

    const [name, setName] = useState('');
    const [price, setPrice] = useState(0);

    const dispatch = useDispatch();

    const handleAddSubmit = useCallback(() => {
        dispatch(addMenuItem(name, price));
        setName('');
        setPrice(0);
        onCancel()
    }, [name, price]);

    return (
        <div>
            <TextField size="small"
                       variant="standard"
                       label=""
                       sx={{
                           p: 2,
                           m: 2,
                           width: '30%'
                       }}
                       type="text"
                       value={name}
                       onChange={e => setName(e.target.value)}
            />

            <TextField
                size="small"
                variant="standard"
                label=""
                sx={{
                    p: 2,
                    m: 2,
                    width: '30%'
                }}
                type="number"
                value={price}
                onChange={e => setPrice(Number(e.target.value))}
            />
            <Button
                variant="outlined"
                size="small"
                sx={{
                    p: 2,
                    m: 2,
                    width: '10%'
                }}
                type="submit"
                onClick={() => {
                    handleAddSubmit();
                }}
            >
                
            </Button>
            <Button
                variant="outlined"
                size="small"
                sx={{
                    p: 2,
                    m: 2,
                    width: '10%'
                }}
                type="button"
                color="warning"
                onClick={onCancel}>
                
            </Button>

        </div>
    )

}
